import React, { Component } from "react";
import "./project.css";
import "../../../node_modules/animate.css/animate.min.css";
import ScrollAnimation from 'react-animate-on-scroll';




class Project extends Component {

    render() {

        return (
            <section id="about">
                <div className="container flex">
                    <div>
                        <ScrollAnimation animateIn="fadeInRight"animateOnce ={true} >
                            <div className="header">
                                PROJECTS
                            </div>
                        </ScrollAnimation>
                        <ScrollAnimation animateIn="fadeInLeft" animateOnce ={true}>
                            <div className="bar">
                                <div className="header-bar waypoint animated"></div>
                            </div>
                        </ScrollAnimation>
                    </div>
                    <div className="card-deck">\
                    <ScrollAnimation animateIn="fadeInUp" animateOnce ={true}>
                        <div className="card">
                            <a href="https://fridge-frenzy.herokuapp.com/" target="blank_">
                                <img src={require('../../img/fridge_frenzy.jpg')} className="card-img-top" alt="Fridge Frenzy App" />
                            </a>
                        </div>
                        </ScrollAnimation>
                        <ScrollAnimation animateIn="fadeInUp" animateOnce ={true}>
                        <div className="card">
                            <a href="https://joshmoore2003.github.io/godsgym/" target="blank_">
                                <img src={require('../../img/gods_gym.jpg')} className="card-img-top" alt="God's Gym Website Template" />
                            </a>
                        </div>
                        </ScrollAnimation>
                    </div>
                    <div className="card-deck">
                    <ScrollAnimation animateIn="fadeInUp" animateOnce ={true}>
                        <div className="card">
                            <a href="https://joshmoore2003.github.io/TriviaGame/" target="blank_">
                                <img src={require('../../img/trivia_game.jpg')} className="card-img-top" alt="Indiana Jones Trivia Game" />
                            </a>
                        </div>
                        </ScrollAnimation>
                        <ScrollAnimation animateIn="fadeInUp" animateOnce ={true}>

                        <div className="card">
                            <a href="http://www.piggy.business/" target="blank_">
                                <img src={require('../../img/piggy_business.jpg')} className="card-img-top" alt="Indiana Jones Trivia Game" />
                            </a>
                        </div>
                        </ScrollAnimation>
                    </div>
                </div>
            </section>
        )
    }
}

export default Project;