import React, { Component } from "react";
import "./footer.css";

class Footer extends Component {

    constructor(props) {
        super(props);
        this.state = {
            firstname: '',
            email: '',
            message: '',
        };
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    };

    handleChange(event) {
        this.setState({ firstname: event.target.firstname });
        this.setState({ email: event.target.email });
        this.setState({ message: event.target.message });
    }

    handleSubmit(event) {
        alert('A name was submitte: ' + this.state.value);
        event.preventDefault();
    }

    render() {

        return (
            <section id="container">

                <div className="float_center">
                    <ul className="child">
                        <li><a href="https://www.linkedin.com/in/jommoore2003/" target="_blank" rel="noopener noreferrer"><i className="fab fa-linkedin fa-spin-hover"></i></a></li>
                        <li><a href="https://github.com/joshmoore2003" target="_blank" rel="noopener noreferrer"><i className="fab fa-github-square fa-spin-hover"></i></a></li>
                        <li><a href="mailto:josh.moore.dezigns@gmail.com"><i className="fas fa-envelope-square fa-spin-hover"></i></a></li>
                    </ul>
                    <div className="clear"></div>
                </div>
                <div className="clear"></div>

                <div className="float_center">
                    <ul className="child">
                        <li><p className="footername">Joshua Moore <span className="copyright">Â©2019</span></p></li>
                    </ul>
                    <div className="clear"></div>
                </div>
                <div className="clear"></div>

            </section>
        )
    }
}

export default Footer;