import React, { Component } from "react";
import "./intro.css";
import Typist from 'react-typist';
import TypistLoop from 'react-typist-loop';
class Intro extends Component {



    render() {

        const divStyleFirstName = {
            color: '#008fff'
        };

        return (
            <section id="home" className="flex height-fix intro-background">
                <div className="intro">
                    <div className="">
                        Hi, my name is <span style={divStyleFirstName}>Joshua Moore</span>.<br />
                        <span>And I'm a </span><TypistLoop interval={1000}>{
                            [
                                'Front-End Web Developer.',
                                'Graphic Designer.',
                                'Photographer.'
                            ].map(text => <Typist key={text} startDelay={0}>{text}
                                <Typist.Backspace count={text.length} delay={1000} />
                            </Typist>)
                        }
                        </TypistLoop>
                    </div>
                    <div className="intro removeDiv">
                        Hi, my name is <span style={divStyleFirstName}>Joshua Moore</span>.<br />
                        And I'm a Front-End Web Developer.
                    </div>
                </div>
            </section>
        )
    }
}

export default Intro;