import React, { Component } from "react";
import "./about.css";
import "../../../node_modules/animate.css/animate.min.css";
import ScrollAnimation from 'react-animate-on-scroll';
import Me from '../../img/me.png';



class About extends Component {
// Animate = (visible) => {
//     if (visible.inViewport) {
//         document.getElementById("html").addClass("line1");
//     }
// }

render() {
    
    return (
        <section id="about">
            <div className="container flex">
                <div>
                    <ScrollAnimation animateIn="fadeInLeft" animateOnce={true}>
                        <div className="header">
                            ABOUT
                            </div>
                    </ScrollAnimation>
                    <ScrollAnimation animateIn="fadeInRight" animateOnce={true}>
                        <div className="bar">
                            <div className="header-bar waypoint animated"></div>
                        </div>
                    </ScrollAnimation>
                </div>
                <div className="parent">
                    <div className="left">
                        <ScrollAnimation animateIn="fadeInLeft" animateOnce={true}>
                            <div className="me">
                                <div><img src={Me} alt="Joshua Moore" width="70%" /></div>
                            </div>
                        </ScrollAnimation>
                    </div>
                    <div className="right">
                        <ScrollAnimation animateIn="fadeInRight" animateOnce={true}>
                            <h2 className="name">Joshua Moore</h2>
                            <p>Front-End Web Developer who has a passion for building state-of-the-art, easy to use websites that result in an improved customer online experience. Love to learn, brainstorm and collaborate with others on projects as well as learn new techniques, processes and programming languages that can provide a new and more efficient way of writing and implementing code.</p></ScrollAnimation>
                    </div>
                </div>
                <div className="wrapper">
                    <ScrollAnimation animateIn="fadeInUp" animateOnce={true}>
                        <h2>HTML</h2>
                        <div className="line line1">90%</div>
                        <h2>CSS</h2>
                        <div className="line line2">80%</div>
                        <h2>JavaScript</h2>
                        <div className="line line3">70%</div>
                        <h2>Node.js</h2>
                        <div className="line line4">70%</div>
                        <h2>React</h2>
                        <div className="line line5">75%</div>
                        <h2>Photoshop</h2>
                        <div className="line line6">85%</div>
                        <h2>Illustrator</h2>
                        <div className="line line7">70%</div>
                    </ScrollAnimation>
                </div>
            </div>
        </section>
    )
}
}

export default About;